.login-main-container {
  width: 100%;
  height: 81.8vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.login-main {
  width: 700px;
  height: 70%;
  background-color: rgb(255, 255, 255);
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid rgb(198, 198, 198);
}
.login-main-left-container {
  width: 40%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.login-main-right-container {
  width: 60%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0 20px 20px 0;
}
.img-pic {
  width: 80%;
  height: 60%;
  border-radius: 20px;
}
.signup-rightside {
  width: 90%;
  height: 90%;
}
.signup-heading-container {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.inputboxs {
  padding: 10px;
  border: none;
  outline: none;
  margin: 5px;
  width: 350px;
  background-color: rgb(225, 233, 255);
  border-radius: 10px;
  margin-left: 30px;
}

.submit-button-container {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}
#submit-btn {
  border: none;
  border-radius: 10px;
  padding: 10px;
  width: 150px;
  cursor: pointer;
  background-color: rgb(191, 187, 219);
  color: rgb(0, 0, 0);
  outline: none;
}

.form-container {
  margin-top: 30px;
}
