.main-contact-container {
  width: 100%;
  height: 81.8vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-contact {
  width: 1200px;
  height: 90%;
  background-color: white;
  border-radius: 20px;
  border: 1px solid rgb(206, 206, 206);
}
.main-contact-left-container {
  width: 40%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-contact-right-container {
  width: 60%;
  height: 100%;
  border-radius: 0 20px 20px 0;
}
.img-pic-contact {
  width: 80%;
  height: 100%;
  border-radius: 20px;
}
.main-contact-heading {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-contact-right {
  width: 100%;
  height: 66%;
  background-color: rgb(48, 187, 187);
}

.main-contact-top-container {
  width: 100%;
  height: 150px;
  /* background-color: aquamarine; */
  border-radius: 10px 10px 0 0;
}

.main-contact-bottom-container {
  width: 100%;
  height: 60%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-contact-right {
  width: 94%;
  height: 100%;
  background-color: rgb(255, 255, 255);
  border-radius: 20px;
  /* border: 1px solid rgb(208, 208, 208); */
}
.message-icon-container {
  width: 130px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.message-icon {
  margin-left: 10px;
}
#message {
  border-radius: 20px;
  height: 90%;
  padding: 10px;
  border: 1px solid rgb(207, 207, 207);
  outline: none;
  background-color: rgb(225, 233, 255);
   
}
.heading-container {
  font-size: 25px;
}
.input-box-container {
  width: 100%;
  height: 60%;
}
.inputbox-container {
  padding: 10px;
  border: none;
  outline: none;
  margin: 5px;
  width: 350px;
  background-color: rgb(225, 233, 255);
  border-radius: 10px;
  margin-left: 30px;
  height: 60px;
}
