.main-home-container{
    width: 100%;
    height:81.8vh ;
    display: flex;
    align-items: center;
    justify-content: center;
}
.main-home-left-container{
height: 100%;
width: 50%;
background-color: rgb(214, 248, 255);
border-right: 1px solid rgb(223, 223, 223);
}
.main-home-right-container{
height: 100%;
width: 50%;
background-color: rgb(245, 245, 245);
}
.position-set-container{
    width: 100%;
    height:81.8vh ;
    position: absolute;
    top: 60px;
    left: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.position-center-container{
    /* margin-top: 150px; */
    width: 100%;
    height: 150px;
}
.welcome-container{
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.name-show-container{
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.wises-container{
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.welcome{
    color: blueviolet;
}
.user-name{
    font-weight: 700;
}
.wises{
    font-weight: 400;
}