.main-about-container {
  width: 100%;
  height: 81.8vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-about {
  width: 1100px;
  height: 92%;
  background-color: white;
  border-radius: 20px;
  border: 1px solid rgb(206, 206, 206);
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-about-left-container {
  width: 35%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-about-right-container {
  width: 65%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-about-left {
  width: 300px;
  height: 90%;
}

.image-container {
  width: 80%;
  height: 150px;
  background-color: rgb(207, 215, 212);
  border-radius: 10px;
  margin-bottom: 20px;
}
.change-photo-container {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(87, 86, 86);
  border-radius: 0 0 10px 10px;
  position: relative;
  top: 110px;
  cursor: pointer;
}
.change-image-container {
  padding: 7px;
  border: none;
  outline: none;
  background: none;
  color: rgb(255, 255, 255);
}
.main-about-right {
  width: 720px;
  height: 90%;
}
.main-about-top-container {
  width: 100%;
  height: 110px;
}
.name-contaner {
  width: 100%;
  height: auto;
  margin: 0%;
}
.profession-contaner {
  width: 100%;
  height: auto;
  margin-bottom: 10px;
}
.profession-name {
  color: rgb(78, 128, 252);
  cursor: default;
}
.p-name {
  cursor: default;
}
.edit-profile-container {
  border: none;
  padding: 10px;
  border-radius: 20px;
  width: 100px;
  position: relative;
  left: 80%;
  bottom: 110px;
  background-color: rgb(217, 217, 217);
  border: 1px solid rgb(207, 207, 207);
}

.main-about-heading-container{
  width: 100%;
  height: 60px;
  border-bottom: 1px solid rgb(235, 235, 235);
  display: flex;
  align-items: flex-end;
}
.heading-container-name{
  width: 100px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
 /* border-bottom:2px solid black ; */
}
.heading-name{
  font-size:18px;
  cursor: pointer;
  font-weight: 600;
}