.signup-main-container {
  width: 100%;
  height: 81.8vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.signupmain {
  width: 900px;
  height: 500px;
  background-color: white;
  border-radius: 20px;
  border: 1px solid rgb(199, 199, 199);
  display: flex;
  align-items: center;
  justify-content: center;
}
.signup-leftside-container {
  width: 40%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.signup-rightside-container {
  width: 60%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0 20px 20px 0;
}
.img-pic {
  width: 80%;
  height: 60%;
  border-radius: 20px;
}
.signup-rightside {
  width: 90%;
  height: 90%;
}
.signup-heading-container {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.gender-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80%;
  height: 40px;
}
.inputbox {
  padding: 10px;
  border: none;
  outline: none;
  margin: 5px;
  width: 230px;
  background-color: rgb(225, 233, 255);
  border-radius: 10px;
}
.input-container {
  margin-top: 20px;
  width: 100%;
  height: 100%;
}

.gender-valid {
  margin: 5px;
}
.male-label {
  margin-left: 20px;
}

.submit-button-container{
    width: 100%;
    height:100px;
    display: flex;
    align-items: center;
    justify-content: center;
}
#submit-btn{
    border: none;
    border-radius: 10px;
    padding: 10px;
    width: 150px;
    cursor: pointer;
    background-color: rgb(191, 187, 219);
    color: rgb(0, 0, 0);
}